/* Suppression des effets avancés problématiques */
.progress-container {
    width: 100%;
    margin: 1rem 0;
  }
  
  .progress-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
    font-size: 0.9rem;
  }
  
  .progress-label {
    font-weight: 600;
  }
  
  .progress-percentage {
    color: #666;
  }
  
  .progress-bar {
    width: 100%;
    background-color: #f0f0f0;
    border-radius: 4px;
    overflow: hidden;
  }
  
  .progress-fill {
    border-radius: 4px;
    transition: width 0.3s ease;
  }